/**
 * 清除浮动 
 * @example scss
 *
 * .element {
 *   @include clearfix;
 * }
 *
 * // CSS Output
 * .element {
 *   zoom: 1;
 * }
 * .element::after {
 *   clear: both;
 *   content: '';
 *   display: block;
 * }
 */
.clearfix {
  zoom: 1;
}
.clearfix::after {
  clear: both;
  content: "";
  display: block;
}
.clearfix .fl {
  float: left;
}
.clearfix .fr {
  float: right;
}

