/**
 * 连续快速点击时禁止选中文字 
 * @example scss
 *
 * .element {
 *   @include noSelect;
 * }
 *
 * // CSS Output
 * .element {
 *   -webkit-touch-callout: none;
 *   -webkit-user-select: none;
 *   -khtml-user-select: none;
 *   -moz-user-select: none;
 *   -ms-user-select: none;
 *   user-select: none;
 * }
 */
.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/**
 * 设置滚动条样式
 * @example scss
 *
 * .element {
 *   @include scrollbar(6px, 6px, #eee, #fff);
 * }
 *
 * // CSS Output
 * .element::-webkit-scrollbar {
 *   width: 6px;
 *   height: 6px;
 * }
 * .element::-webkit-scrollbar-thumb {
 *   border-radius: 100px;
 *   background-color: #eee;
 * }
 * .element::-webkit-scrollbar-track {
 *   border-radius: 0;
 *   background-color: #fff;
 * }
 */
html::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
html::-webkit-scrollbar-thumb {
  border-radius: 100px;
  background-color: #ccc;
}
html::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: #fff;
}

.btn {
  width: 100%;
  height: 40px;
  display: block;
  outline: 0;
  text-align: center;
  color: #fff;
  font-size: 16px;
  line-height: 38px;
  background-color: #0078E7;
  border-radius: 2px;
  border: 1px solid #0078E7;
  margin: 0;
  padding: 0 8px;
  outline: 0;
  cursor: pointer;
  transition: border-color, color, background-color, border-radius, opacity, font-weight 0.3s ease-in-out;
  font-weight: 400;
  opacity: 1;
  filter: alpha(opacity=100);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
